const initAccordion = () => {
  const accordionBlocks = document.querySelectorAll('.accordion');

  if (accordionBlocks) {
    accordionBlocks.forEach((block) => {
      const blockItems = block.querySelectorAll('.accordion__item');
      const btn = block.querySelector('.benefit__button');

      blockItems.forEach((item) => {
        item.classList.remove('accordion__item--shown');
      });
      btn.classList.remove('benefit__button--arrow-rotate');

      btn.addEventListener('click', () => {
        blockItems.forEach((item) => item.classList.toggle('accordion__item--shown'));
        btn.classList.toggle('benefit__button--arrow-rotate');
      });
    });
  }
};

export {initAccordion};
