/**
 *
 * устанавливает в benefit.html изначальный контент(для первого направления modal-professional)
 *
 *  */

const setBenefit = () => {
  if (window.location.hash !== '#bodily' && window.location.hash !== '#emotionally' && window.location.hash !== '#social') {
    // устанавливает изначально отображаемое изображение
    const pictureDefault = document.querySelector('.modal-professional-picture');
    if (pictureDefault) {
      pictureDefault.classList.add('active-picture');
    }

    // устанавливает изначально отображаемый список
    const listDefault = document.querySelector('.modal-professional-list');
    if (listDefault) {
      listDefault.classList.add('active-list');
    }

    // устанавливает изначально отображаемый заголовок
    const titleDefault = document.querySelector('#modal-professional-title');
    if (titleDefault) {
      titleDefault.classList.add('active-title');
    }
  }
};

/**
 *
 * переходит к нужному hash эмулируя клик
 *
 *  */

const getLocation = () => {
  // находит id радио-кнопки (tabs)
  const btnProfessional = document.querySelector('#benefit-professional');
  const btnBodily = document.querySelector('#benefit-bodily');
  const btnEmotionally = document.querySelector('#benefit-emotionally');
  const btnSocial = document.querySelector('#benefit-social');

  if (window.location.hash === '#bodily') {
    btnProfessional.removeAttribute('checked');
    btnBodily.setAttribute('checked', 'true');
    const titleDefault = document.querySelector('#modal-bodily-title');
    const pictureDefault = document.querySelector('.modal-bodily-picture');
    const listDefault = document.querySelector('.modal-bodily-list');
    titleDefault.classList.add('active-title');
    pictureDefault.classList.add('active-picture');
    listDefault.classList.add('active-list');
  }
  if (window.location.hash === '#emotionally') {
    btnProfessional.removeAttribute('checked');
    btnEmotionally.setAttribute('checked', 'true');
    const titleDefault = document.querySelector('#modal-emotionally-title');
    const pictureDefault = document.querySelector('.modal-emotionally-picture');
    const listDefault = document.querySelector('.modal-emotionally-list');
    titleDefault.classList.add('active-title');
    pictureDefault.classList.add('active-picture');
    listDefault.classList.add('active-list');
  }
  if (window.location.hash === '#social') {
    btnProfessional.removeAttribute('checked');
    btnSocial.setAttribute('checked', 'true');
    const titleDefault = document.querySelector('#modal-social-title');
    const pictureDefault = document.querySelector('.modal-social-picture');
    const listDefault = document.querySelector('.modal-social-list');
    titleDefault.classList.add('active-title');
    pictureDefault.classList.add('active-picture');
    listDefault.classList.add('active-list');
  }
};

export {setBenefit, getLocation};
