// benefit.html
const initCounter = () => {
  const container = document.querySelectorAll('.accordion');

  if (container) {
    container.forEach((block) => {
      let lengthContainer = block.children.length;
      let amount = lengthContainer - 1;

      const span = block.querySelector('.benefit__span-number');
      span.innerHTML = amount;
    });
  }
};

export {initCounter};
