/**
 *
 * Меняет контент на странице benefit.html
 *
 * пример использования
 * changeContent(modalId, '-list', '.modal-list', 'active-list');
 *
 * @param {string} modalId - id блока, который нужно изменить
 * @see modalId - связь с modal.js, а именно функцией changeBenefit()
 *
 * @param {string} modalIdApp - modalID data-id="modal-professional",
 *  а контент который нужно открыть id="modal-professional-title", id="modal-professional-picture", id="modal-professional-list"
 *  поэтому к modalID добавляем modalIdName -title, -picture, -list
 *
 *
 * @param {string} modalClass - класс блока, который нужно изменить
 * @param {string} activeClass - класс который отобразит выбранный контент
 *
 *  */

const changeContent = (modalId, modalIdApp, modalClass, activeClass) => {
  const elements = document.querySelectorAll(modalClass); // находит блок, который нужно поменять
  elements.forEach((item) => {
    if (item.classList.contains(activeClass)) {
      item.classList.remove(activeClass); // скрывает весь контент в этом блоке, который был ранее открыт
    }
  });

  let modalPath = modalId + modalIdApp;
  let modal = document.getElementById(modalPath); // находит модальное окно которое надо открыть
  modal.classList.add(activeClass); // добавляет класс active, что позволяет изменить контент
};


export {changeContent};
