import {changeContent} from '../utils/change-content';
import {setBenefit, getLocation} from '../utils/location';

// меняет контент в benefit.html при клике на радио-кнопки
const changeBenefit = () => {
  const btn = document.querySelectorAll('.js-button-tabs');

  if (btn) {
    btn.forEach((item) => {
      item.addEventListener('click', (evt) => {
        const input = evt.target;

        const modalId = input.dataset.id;

        const modalHash = input.dataset.hash;
        window.location.hash = modalHash;

        changeContent(modalId, '-picture', '.modal-picture', 'active-picture');
        changeContent(modalId, '-list', '.modal-list', 'active-list');
        changeContent(modalId, '-title', '.modal-title', 'active-title');
      });
    });
  }

  setBenefit(); // устанавливает в benefit.html изначальный контент
  getLocation(); // переходит от main.html к нужному направлению
};

export {changeBenefit};
