const hideBonus = () => {

  const directions = document.querySelector('.directions');
  const benefit = document.querySelector('.benefit');

  if (directions) {
    const KEY = 'hide_badge';
    const badge = directions.querySelectorAll('span[data-bonus="bonus"]');

    const isHidden = Boolean(sessionStorage.getItem(KEY));

    if (isHidden) {
      badge.forEach((item) => {
        item.setAttribute('hidden', 'true');
      });
    }

    sessionStorage.setItem(KEY, 'true');
  }

  // benefit.html
  if (benefit) {
    const KEY = window.location.pathname;
    const badge = benefit.querySelectorAll('span[data-bonus="bonus"]');

    const isHidden = Boolean(sessionStorage.getItem(KEY));

    if (isHidden) {
      badge.forEach((item) => {
        item.setAttribute('hidden', 'true');
      });
    }

    sessionStorage.setItem(KEY, 'true');

    badge.forEach((item) => {
      if (item.hasAttribute('hidden')) {
        const accordionType = benefit.querySelectorAll('.accordion__type-container');
        const accordionLink = benefit.querySelectorAll('.accordion__link');
        const accordionQrLink = benefit.querySelectorAll('.accordion__qr-link');

        accordionType.forEach((el) => {
          if (el.classList.contains('accordion__type-container--orange')) {
            el.classList.remove('accordion__type-container--orange');
          }
        });

        accordionLink.forEach((el) => {
          if (el.classList.contains('accordion__link--orange')) {
            el.classList.remove('accordion__link--orange');
          }
        });

        accordionQrLink.forEach((el) => {
          if (el.classList.contains('accordion__qr-link--orange')) {
            el.classList.remove('accordion__qr-link--orange');
          }
        });
      }
    });
  }

};

export {hideBonus};
